body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
    margin: 0;
    padding: 0;
    -webkit-user-select: none;
            user-select: none;
}
.Left_panel > ul {
    list-style: none;
    font-size: calc(0.4rem + 1vw);
    padding: 0;
}
.tab {
    height: 50px;
    color: black;
    padding: 10px;
    width: 15vw;
}
li.tab:hover {
    background-color: skyblue;
    font-size: 110%;
    cursor: pointer;
}

.main_box {
    display: flex;
    align-content: space-around;
}
.Quiz_panel {
    background: url(/Quiz_Site/static/media/background.2c6b7849.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    width: 85vw;
    text-align: center;
    overflow-y: hidden;

}
.Left_panel {
    height: 100vh;
    width: 15vw;
    overflow: hidden;
}

.navbar {
    height: 12vh;
}
.Score_holder {
    text-align: center;
    width: 100vh;
    position: relative;
    left: 30%;
    top: 30vh;
}
.quiz-box {
    position: relative;
    width: 50vw;
    height: 60vh;
    border: 5px solid white;
    border-radius: 50px;
    text-align: center;
    left: 18%;
    top: 10%;
    z-index: 2;
    background-color: white;
}
.question {
    position: relative;
    top: 10%;
}
.option-box {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    top: 20%;
    left: 20%;
    width: 30vw;			
}
#opt1, #opt2, #opt3, #opt0 {
    top: 5%;
    left: 4%;
    position: relative;
    width: 30%;
    height: 7vh;
    margin: 5% 10% 5% 10%;
    font-weight: bolder;
}
.quiz-box-copy1 {
    position: absolute;
    width: 50vw;
    height: 60vh;
    background-color: lightgreen;
    border-radius: 50px;
    left: 30%;
    -webkit-transform: rotate(2deg);
            transform: rotate(2deg);
    top: 31%;
    z-index: 1;
}
.quiz-box-copy2 {
    position: absolute;
    width: 50vw;
    height: 60vh;
    background-color: rgb(239, 90, 90);
    border-radius: 50px;
    left: 31%;
    -webkit-transform: rotate(-2deg);
            transform: rotate(-2deg);
    top: 31%;
    z-index: 0;
}

.question-number {
    font-size: 40px;
    position: absolute; 
    top: 88%;
    left: 40%;
}

.mt-1 {
    text-decoration: underline;
    text-underline-offset: 5px;
}

@-webkit-keyframes success {
    0% { 
        -webkit-transform: rotate(0deg); 
                transform: rotate(0deg); 
    } 30% { 
        -webkit-transform: rotate(5deg); 
                transform: rotate(5deg); 
    }
        60%{
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        100%{
            -webkit-transform: rotate(-5deg);
                    transform: rotate(-5deg);
        }
}

@keyframes success {
    0% { 
        -webkit-transform: rotate(0deg); 
                transform: rotate(0deg); 
    } 30% { 
        -webkit-transform: rotate(5deg); 
                transform: rotate(5deg); 
    }
        60%{
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        100%{
            -webkit-transform: rotate(-5deg);
                    transform: rotate(-5deg);
        }
}   
@-webkit-keyframes failure {
    0% { 
        -webkit-transform: rotate(0deg); 
                transform: rotate(0deg); 
    } 30% { 
        -webkit-transform: rotate(-5deg); 
                transform: rotate(-5deg); 
    }
        60%{
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        100%{
            -webkit-transform: rotate(5deg);
                    transform: rotate(5deg);
        }
}   
@keyframes failure {
    0% { 
        -webkit-transform: rotate(0deg); 
                transform: rotate(0deg); 
    } 30% { 
        -webkit-transform: rotate(-5deg); 
                transform: rotate(-5deg); 
    }
        60%{
            -webkit-transform: rotate(0deg);
                    transform: rotate(0deg);
        }
        100%{
            -webkit-transform: rotate(5deg);
                    transform: rotate(5deg);
        }
}   

@media only screen and (max-width: 600px) {
    .option-box {
        top: 15%;
        left: 20%;
        width: 70vw;			
    }
    #opt1, #opt2, #opt3, #opt0 {
        width: 120px;
    }
    .Left_panel {
         display: none;
     }
    
    .nav-item {
        text-align: right;
        font-weight: bold;
    }
    .quiz-box {
        width: 85vw;
        height: 65vh;
        left: 5%;
        top: 10%;
    }
    .quiz-box-copy1 {
        width: 86vw;
        height: 65vh;
        left: 7%;
        top: 31%;
    }
    .quiz-box-copy2 {
        width: 86vw;
        height: 65vh;
        left: 7%;
        top: 31%;
    }
    .Quiz_panel {
        background: url(/Quiz_Site/static/media/mobile_bg.f8cbdad4.jpg);
        background-size: cover;
        width: 100vw;
    }
    .mt-1, .question-number {
        color: rgb(237, 217, 217);
    }
    .question-number {
        font-size: 40px;
        top: 105%;
        left: 27%;
    }
    .Score_holder {
        width: 46vh;
        position: absolute;
        left: 1%;
        top: 40vh;
    }
}
